



























































	// 选择时间的选择
	import MxDatePicker from '../../../components/mx-datepicker/mx-datepicker.vue'
	
	import dateutll from '../../../common/util.js'
	// 添加上推的列表选择
	import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
	// 添加图片转换组件
	import { pathToBase64, base64ToPath } from '../../../components/image-tools/index.js'
	export default {
		components: {
			MxDatePicker,
			mpvuePicker
		},
		data() {
			return {
				pickerSingleArray: [{
						label: '中国',
						value: 1
					},
					{
						label: '俄罗斯',
						value: 2
					},
					{
						label: '美国',
						value: 3
					},
					{
						label: '日本',
						value: 4
					}
				],
				'textnum':'0/30',
				'inputresean':'',
				'shenpiperson':'',
				 zhengmingren:'',
				'haverull':false,
				zhuangtai:'审批中',
				jifen:'',
				// 时间选择器需要的数据
				showPicker: false,
				date: '2019/01/01',
				type: 'date',
				value: '',
				//关于选择联系人的值
				themeColor: '#007AFF',
				pickerText: '',
				mode: '',
				deepLength: 1,
				pickerValueDefault: [0],
				pickerValueArray: [],
				//关于拍照和从相册选择图片
				imageList: [],
				countIndex: 8,
				count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
				base64:''
			};
		},
		onReady(){
			this.date=dateutll.dateUtils.getNowFormatDate();
			this.inputresean='输入理由';
			this.shenpiperson='审批人';
			this.zhengmingren='小明';
			this.textnum=this.inputresean.length+'/30';
			
		},
		onUnload() {
			this.imageList = [],
// 				this.sourceTypeIndex = 2,
// 				this.sourceType = ['拍照', '相册', '拍照或相册'],
// 				this.sizeTypeIndex = 2,
// 				this.sizeType = ['压缩', '原图', '压缩或原图'],
				this.countIndex = 8;
		},
		methods:{
			inputholder:function(e){
				let length=this.inputresean.length;
				this.textnum=length+'/'+'30'
			},
			choisetime:function(type){
				//弹出时间的选择框
				this.type = type;
				this.showPicker = true;
				this.value = this[type];
			},
			choiseperson:function(){
				// 选择审批人
				this.pickerValueArray = this.pickerSingleArray
				this.mode = 'selector'
				this.deepLength = 1
				this.pickerValueDefault = [0]
				this.$refs.mpvuePicker.show()
				
			},
			switchchange:function(e){
				 this.haverull=e.target.value;
			},
			addshenqing:function(){
				//添加上传图片  转化为base64 字符串的方法  这里是单个的转化为base64的方法
				 pathToBase64(this.imageList[0])
							.then(base64 => {
								console.log(base64)
								this.base64 = base64
							})
							.catch(error => {
								console.error(error)
							})
 
			},
			addimage:function(){
				if (this.imageList.length === 9) {
					 uni.showToast({
					 	title: '最多上传九张照片',
					 	mask: false,
					 	duration: 1500,
						icon:"none"
					 });
					 return
				}
				uni.chooseImage({
					sourceType: ['camera', 'album'],
					sizeType: ['compressed'],
					count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				})
				
			},
			showbigimage:function(e){
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			onSelected(e) {//选择
				this.showPicker = false;
				if(e) {
					this[this.type] = e.value; 
				}
			},
			// 点击选择负责人的列表的展示时的确认
			onCancel(e) {
				console.log(e)
			},
			onConfirm(e) {
				
				 this.shenpiperson=e.label;
			},
			  close(e){
			     this.imageList.splice(e,1);
			 },
		}
	}
